import React,{ Component } from 'react'
import { withRouter } from 'react-router-dom'
import './pageFooter.less'
import { Row, Col } from 'antd'
import qrcode from '../../images/qrcode.png'

class pageFooter extends Component {
    render() {
        return (
            <div className="common-footer">
                <div className="home-flag">
                    <Row type="flex">
                        <Col span={6}>
                            <div className="flag-img-layout flag-img0"></div>
                            <h2 className="flag-title"></h2>
                            <p className="flag-text"></p>
                        </Col>
                        <Col span={6}>
                            <div className="flag-img-layout flag-img1"></div>
                            <h2 className="flag-title"></h2>
                            <p className="flag-text"></p>
                        </Col>
                        <Col span={6}>
                            <div className="flag-img-layout flag-img2"></div>
                            <h2 className="flag-title"></h2>
                            <p className="flag-text">100%</p>
                        </Col>
                        <Col span={6}>
                            <div className="flag-img-layout flag-img3"></div>
                            <h2 className="flag-title"></h2>
                            <p className="flag-text">PCO</p>
                        </Col>
                    </Row>
                </div>
                <div className="footer-layout">
                    <div className="footer-content">
                        <Row type="flex" justify="start">
                            <Col span={6}>
                                <h2 className="footer-title"></h2>
                                <p className="footer-text">
                                     18874977350<br/>
                                    QQ : 491263438<br/>
                                     : 491263438@qq.com<br/>
                                     : 2108425204@qq.com
                                </p>
                            </Col>
                            <Col span={12}>
                                <h2 className="footer-title"></h2>
                                <p className="footer-text">
                                <br/>- -;<br/>
        <br/>- 
                                </p>
                            </Col>
                            <Col span={6}><img src={qrcode} alt="" /></Col>
                        </Row>
                        <div className="copy-right">ICP18016855-1<br/>
        :A188203</div>
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(pageFooter)


