import React, { Component } from 'react'
import { Row, Col, Pagination } from 'antd'
import './case.less'
import p1 from '../../images/place1.png'

class Case extends Component {
    constructor(props) {
        super(props)
        this.state = {
            current: 1,
        }
    }
    onChange(page) {
        this.setState({
            current: page,
        });
    }

    render(){
        return (
            <div className="main">
                <div className="content-layout">
                    <div className="common-title"></div>
                    <Row type="flex" justify="start" className="commoon-news-list">
                        <Col span={8} className="commoon-news-item">
                            <img src={p1} alt="" className="commoon-news-img"></img>
                            <h2 className="commoon-news-title"> </h2>
                            <p className="commoon-news-desc">5</p>
                            <div className="tag"> </div>
                        </Col>
                        <Col span={8} className="commoon-news-item">
                            <img src={p1} alt="" className="commoon-news-img"></img>
                            <h2 className="commoon-news-title"> </h2>
                            <p className="commoon-news-desc">5</p>
                            <div className="tag"> </div>
                        </Col>
                        <Col span={8} className="commoon-news-item">
                            <img src={p1} alt="" className="commoon-news-img"></img>
                            <h2 className="commoon-news-title"> </h2>
                            <p className="commoon-news-desc">5</p>
                            <div className="tag"> </div>
                        </Col>
                    </Row>
                    <Row type="flex" justify="start" className="commoon-news-list">
                        <Col span={8} className="commoon-news-item">
                            <img src={p1} alt="" className="commoon-news-img"></img>
                            <h2 className="commoon-news-title"> </h2>
                            <p className="commoon-news-desc">5</p>
                            <div className="tag"> </div>
                        </Col>
                        <Col span={8} className="commoon-news-item">
                            <img src={p1} alt="" className="commoon-news-img"></img>
                            <h2 className="commoon-news-title"> </h2>
                            <p className="commoon-news-desc">5</p>
                            <div className="tag"> </div>
                        </Col>
                    </Row>
                    <Pagination current={this.state.current} onChange={this.onChange.bind(this)} total={50} />
                </div>
            </div>
        )
    }
}

export default Case


